let token = localStorage.token
let formdata = new FormData()
formdata.append("yb_userid",this.yb_userid)
formdata.append("userid",this.userid)
formdata.append("username",this.name)
formdata.append("sex",this.defaultSex)
formdata.append("idCardNum",this.idCardNum)
formdata.append("nation",this.nationValue)
formdata.append("birthday",this.birthday)
formdata.append("CBRQ",this.valueReadonly)
formdata.append("XZ",this.XZ)
formdata.append("BJMC",this.BJMC)
formdata.append("mobile",this.mobile)
formdata.append("HJSZD",this.HJSZD)
formdata.append("JTZC",this.JTZC)
formdata.append("contactPerson",this.contactPerson)
formdata.append("contactPersonMobile",this.contactPersonMobile)
formdata.append("special",this.special)

let data = await postFormApi(token,formdata)

this.showToast = true
this.toastText = data.info.msg
if(data.status == "error"){
this.toastType = "cancel"
}else if(data.status == "success"){
this.toastType = ""
this.$router.push({ path:'/Success',query:{verify_request:token}})
}
this.showLoading = false
})()
},
onCancel () {
console.log('on cancel')
},
onFocus () {
console.log('on focus')
}

},
